body {
  background: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

form {
  max-width: 500px;
  margin: 0 auto;
}

h1 {
  font-weight: 100;
  color: rgb(55, 131, 197);
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(79, 98, 148);
}

.form {
  background: #fff;
  margin: 0 auto;
  margin-left: 5%;
  width: 50%;
}

p {
  color: rgb(55, 131, 197);
}

input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0 0px 5px 0 rgba(55, 131, 197, 0.3), 0 0px 5px 0 rgba(55, 131, 197, 0.3);
  border: 0px solid rgba(55, 131, 197, 0.3);
  padding: 10px 15px;
  margin-bottom: 10px;
  font-size: 18px;
}

label {
  line-height: 2;
  text-align: left;
  display: block;
  margin-bottom: 13px;
  margin-top: 20px;
  color: black;
  font-size: 19px;
  font-weight: 200;
}

button[type="submit"],
input[type="submit"] {
  background: rgb(55, 131, 197);
  color: white;
  text-transform: uppercase;
  border: none;
  margin-top: 40px;
  padding: 20px;
  font-size: 16px;
  font-weight: 100;
  letter-spacing: 10px;
}

button[type="submit"]:hover,
input[type="submit"]:hover {
  background: rgb(35, 111, 177);
}

button[type="submit"]:active,
input[type="button"]:active,
input[type="submit"]:active {
  transition: 0.3s all;
  transform: translateY(3px);
  border: 1px solid transparent;
  opacity: 0.8;
}

input:disabled {
  opacity: 0.4;
}

input[type="button"]:hover {
  transition: 0.3s all;
}

button[type="submit"],
input[type="button"],
input[type="submit"] {
  -webkit-appearance: none;
}

.App {
  max-width: 600px;
  margin: 0 auto;
}

button[type="button"] {
  display: block;
  appearance: none;
  background: #333;
  color: white;
  border: none;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 4px;
}

hr {
  margin-top: 30px;
}

button {
  display: block;
  appearance: none;
  margin-top: 40px;
  border: 1px solid #333;
  margin-bottom: 20px;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 4px;
}

.column {
  float: left;
}

ul {
  width: 15%;
}
img.partner-logo {
  width: 300px;
}

code {
  background-color: #eee;
  border: 1px solid #ccc;
  float: left;
  width: 20%;
  white-space: pre-wrap;
  padding:10px;
  font-family: monospace;
}

section div p {
  border: 2px dashed #eee;
  padding: 20px;
  background-color: #fcfcfc;
  color: #959595;
}

img.logo {
  width:150px;
}

img.screen-image {
  width:250px;
}